















































import Vue from "vue";
import Component from "vue-class-component";
import { AppService } from "../service/app.service";
import CafeCreateBanner from "./cafe/CafeCreateBanner.vue";
import AdvertisementBanner from "@/x-vue/components/advertisement/AdvertisementBanner.vue";
import OpenWeatherMap from "@/x-vue/components/weather/OpenWeatherMap.vue";
import CurrencyConverter from "@/x-vue/components/currency-converter/CurrencyConverter.vue";

@Component({
  components: {
    CafeCreateBanner,
    AdvertisementBanner,
    OpenWeatherMap,
    CurrencyConverter,
  },
})
export default class App extends Vue {
  app = AppService.instance;

  logout(): void {
    this.app.logout();
  }

  openCafe(id: string): void {
    location.href = "https://" + id + ":" + location.port + "/cafe/admin/edit";

    // <router-link :to="'/cafe/admin/edit/' + cafe.id">{{
    //   cafe.id
    // }}</router-link>
  }
}
