import Vue from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";

import {
  AvatarPlugin,
  AlertPlugin,
  ButtonPlugin,
  ProgressPlugin,
  SpinnerPlugin,
  FormTextareaPlugin,
  NavbarPlugin,
  FormInputPlugin,
  PopoverPlugin,
  TabsPlugin,
  CardPlugin,
  ModalPlugin,
  PaginationNavPlugin,
  ToastPlugin,
  FormSelectPlugin,
  FormCheckboxPlugin,
  FormGroupPlugin,
} from "bootstrap-vue";

// Import Bootstrap an BootstrapVue CSS files (order is important)
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

import { AppService } from "./service/app.service";
import Cookies from "js-cookie";
import $ from "jquery/dist/jquery.slim";

import "./scss/index.scss";

// import { tr } from "@/x-vue/services/functions";
import { XHelper } from "./x-vue-helper/x-helper";
import "./x-vue-helper/x-helper.css";

import { ApiService } from "./x-vue/services/api.service";
import { UserModel } from "./x-vue/services/interfaces";

import { texts } from "@/service/translation";

import { FirebaseService } from "./x-vue/services/firebase.service";

import ComponentService, {
  ConfirmToast,
  PLACEMENT,
  Toast,
} from "./x-vue/services/x-vue.service";

Vue.config.productionTip = false;

// Make BootstrapVue available throughout your project
Vue.use(AvatarPlugin);
Vue.use(AlertPlugin);
Vue.use(ButtonPlugin);
Vue.use(ProgressPlugin);
// Vue.use(IconsPlugin);
Vue.use(NavbarPlugin);
Vue.use(FormInputPlugin);
Vue.use(SpinnerPlugin);
Vue.use(FormTextareaPlugin);
Vue.use(PopoverPlugin);
Vue.use(TabsPlugin);
Vue.use(CardPlugin);
Vue.use(ModalPlugin);
Vue.use(PaginationNavPlugin);
Vue.use(ToastPlugin);

Vue.use(FormCheckboxPlugin);
Vue.use(FormSelectPlugin);
Vue.use(FormGroupPlugin);

// Optionally install the BootstrapVue icon components plugin
// Vue.use(IconsPlugin);

const app = AppService.instance;
const x = XHelper.instance;

Vue.prototype.$app = app;

const vm = new Vue({
  router,
  store,
  data: {
    a: "apple",
    landing: true,
  },
  render: (h) => h(App),
  beforeCreate: function () {
    ``;
  },
  mounted: async function () {
    ``;
  },
  methods: {
    aTag: function (href: string) {
      this.$router.push(href).catch(() => null);
    },
    changeLanguage: function (v: string) {
      app.setCookie("language", v);
      location.reload();
    },
  },
}).$mount("#app");

store.state.router = router;
x.init({ vm: vm });

ApiService.instance.init({
  userChanges: (user: UserModel) => {
    store.state.user = user;

    // @todo test needed.
    // Save firebase token to backend when user visit as logged in, login, register, profile update.
    if (user.loggedIn) {
      if (FirebaseService.instance.token) {
        ApiService.instance.saveToken(
          FirebaseService.instance.token,
          location.hostname
        );
      }
    }
  },
  texts: texts,
});

ComponentService.instance.init({
  alert: (t: string, m: string) => x.alert(t, m),
  confirmToast: (options: ConfirmToast) => x.confirmToast(options),
  toast: (options: Toast) => x.toast(options),
  confirm: (t: string, m: string) => x.confirm(t, m),
});

FirebaseService.instance.init(
  {
    apiKey: "AIzaSyDWiVaWIIrAsEP-eHq6bFBY09HLyHHQW2U",
    authDomain: "sonub-version-2020.firebaseapp.com",
    databaseURL: "https://sonub-version-2020.firebaseio.com",
    projectId: "sonub-version-2020",
    storageBucket: "sonub-version-2020.appspot.com",
    messagingSenderId: "446424199137",
    appId: "1:446424199137:web:f421c562ba0a35ac89aca0",
    measurementId: "G-F86L9641ZQ",
  },
  {
    onMessage: (payload: Record<string, Record<string, unknown>>) => {
      const notification = payload.notification;
      const data = payload.data ? payload.data : {};
      let url = "";

      // check if the sender is current loggedIn user
      if (store.state.user.idx == data["senderIdx"]) return;

      /**
       * If the type is post then move it to a specific post.
       */
      if (data["type"] === "post") {
        url = "/" + data["idx"];
      }

      /**
       * If the type is chat then move it to private chat.
       */
      if (data["type"] === "chat") {
        url = "/chat/" + data["idx"];
      }

      // show toast
      ComponentService.instance.toast({
        title: notification.title as string,
        message: notification.body as string,
        placement: PLACEMENT.BOTTOM_RIGHT,
        hideDelay: 10000,
        clickCallback: () => {
          if (store.state.router.currentRoute.path == url) {
            // return store.state.router.go(0);
            return store.commit("postViewKey");
          }
          store.state.router.push(url);
        },
      });
    },
  }
);

$("a").click(function (e) {
  e.preventDefault();
  vm.aTag($(this).attr("href") as string);
});

$(function () {
  const ln = Cookies.get("language");
  if (ln) {
    $(".desktop-topmenu [name='language']").val(ln);
  }
});
